.navbar{
    display: flex;
    justify-content: space-between;
    background-color: red;
    color: white;
    font-size: large;
    font-weight: 600;
}


.logo{
    width: 120px;
    height: auto;
    margin: 20px;
    background-color: white;
}

.left{
    display: flex;
    align-items: center;
    flex: 1 1 auto;
}

.right{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex: 1 1 auto;
}


.li:hover{
    text-decoration: underline;
    cursor: pointer;
    font-weight: 700;
}